import Preview, { PreviewInstance, PreviewInstanceCallback, PreviewInterface } from './ImgPreview'
import './style.scss'

let ins: PreviewInstance = null

/**
 * 获取ImgPreview实例
 *
 * @param {Function} callback 回调
 */
function getInstance(callback: PreviewInstanceCallback) {
  if(ins) {
    callback(ins)
    return
  }
  Preview.newInstance((instance: PreviewInstance) => {
    ins = instance
    callback(instance)
  })
}

/**
 * 高阶函数
 * 绑定ins相应的方法并返回
 * @param {String} fun 方法名
 * @returns Function
 */
function exec(fun: string) {
  return function(...arg) {
    getInstance(instance => {
      instance[fun](...arg)
    })
  }
}

const Api: {destroy: () => void, preview: PreviewInterface, show: () => void, hide: () => void} = {
  preview: null,
  show: null,
  hide: null,
  destroy() {
    if(ins) {
      ins.destroy()
      ins = null
    }
  }
}

const func = ['preview', 'show', 'hide']
func.forEach(each => {
  Api[each] = exec(each)
})

export const PreviewApi = Api

export default Preview
try {
    // @ts-ignore
    ImgPreview.displayName = "ImgPreview";
    // @ts-ignore
    ImgPreview.__docgenInfo = { "description": "", "displayName": "ImgPreview", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/lib/ImgPreview/index.ts#ImgPreview"] = { docgenInfo: ImgPreview.__docgenInfo, name: "ImgPreview", path: "src/lib/ImgPreview/index.ts#ImgPreview" };
}
catch (__react_docgen_typescript_loader_error) { }